@import url('https://fonts.googleapis.com/css2?family=Belanosima&family=Ubuntu:wght@300&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: ubuntu;
}

.principale {
  text-align: center;
  /* background-color: rgb(39, 55, 77); */
  /* background: linear-gradient(#085866, #44bacf, #ecedf0); */
  background: linear-gradient(0.25turn, #085866, #44bacf, #92d9e6, #ecedf0);
  /* background-image: url("./img/wallpaper-2158331_1280.jpg"); */
  height: 40rem;
  background-size: cover;
  color: rgb(2, 22, 37);
  
}

.links {
  display: flex;
  text-decoration: none;
  gap: 2rem;
  justify-content: center;
  margin-bottom: 2rem;
  font-size: 1.2rem;
  background-color: white;
}

.links:visited {
  color: white;
}

.name {
  font-size: 2.7rem;
  margin-top: 13rem;
}

.name p {
  color: rgb(2, 22, 37);
  font-weight: bold;
}

.photo {
  border-radius: 40%;
  -webkit-border-radius: 40%;
  -moz-border-radius: 40%;
  -ms-border-radius: 40%;
  -o-border-radius: 40%;
  width: auto;
  height: 17rem;
}

.container {
  display: flex;
  gap: 2rem;
  /* background-image: url("./img/Abstract-Watercolor-Background-Design-Graphics-19383757-1.jpg"); */
  background: linear-gradient(0.25turn, #085866, #44bacf, #92d9e6, #ecedf0);
  padding: 2rem;
  color: rgb(2, 22, 37);
  height: 40rem;
  /* width: 50%; */
  justify-content: center;
  text-align: center;
}

.works {
  width: auto;
  height: auto;
}

/* .space {
  height: 0.5rem;
  width: 100%;
  background-color: rgb(5, 5, 65);
  margin: 1rem 0;
} */

.contact {
  display: flex;
  flex-direction: column;
  justify-content: top;
  align-items: center;
  /* border: 1px black inset; */
  /* background-image: url("./img/Abstract-Watercolor-Background-Design-Graphics-19383757-1.jpg"); */
  background: linear-gradient(0.25turn, #085866, #44bacf, #92d9e6, #ecedf0);
  background-size: cover;
  color: rgb(2, 22, 37);
  height: 40rem;
}

.contact a:visited {
  color: rgb(2, 22, 37);
}

.fond {
  text-align: center;
  width: 100%;
}
.fond img {
  width: 400px;
}

@media screen and (min-width: 1200px) {

.fond img {
  width: auto;
}

.works {
  display: flex;
  justify-content: space-between;

}

.works img {
  width: 450px;
}
}